/* Box Model Hack */
* {
    box-sizing: border-box;
}

/* Clear fix hack */
.clearfix:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
}

.clear {
   clear: both;
}

/******************************************
/* BASE STYLES
/*******************************************/

body {
    display: flex;
    min-height: 80%;
    justify-content: center;
    align-items: center;
    height: 100vh;
    
}


/******************************************
/* LAYOUT
/*******************************************/

html{
    background: rgb(238, 238, 238) no-repeat center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

#root {
    padding: 2% 0 0 0;
    min-width: 350px;
    min-height: 750px;
    max-height: 79vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

#Calc-Div {
    min-width: 325px;
    border: solid black thick;
    padding: 8% 8%;
    border-radius: .5rem;
    background: lightblue;
    min-height: fit-content;
}



.calc-outline{
    
    border: solid lightblue;
    border-radius: .5rem;
    display: grid;
    height: 100%;
    min-height: 100%;
    max-height: 100%;
    
    display: grid; 
    grid-template-columns: 1fr 1fr; 
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
    gap: 1rem 0px; 
}


.answer-cont{
    padding: .5rem .5rem;
    text-align: right;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 2fr;
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: 3;
    background: rgb(238, 238, 238);
    border-radius: .5rem;
    border: solid black .4rem;
}

.number-cont{
    gap: .1rem;
    grid-row-start: 5;
    grid-row-end: 8;
    grid-column-start: 1;
    grid-column-end: 3;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr; 
    grid-template-rows: 1fr 1fr 1fr 1fr;

}

.operator-cont{
    gap: .25rem;
    grid-row-start: 3;
    grid-row-end: 5;
    grid-column-start: 1;
    grid-column-end: 3;

    display:grid;
    grid-template-columns: 1fr 1fr; 
    grid-template-rows: 1fr 1fr 1fr;

}

.btn{
    background-color: rgb(127, 187, 187);
    transition-property: background-color;
    transition-duration: .15s;
    border-radius: .5rem;
    border: black solid thick;
    font-size: 2rem;
}

.btn-hover{
    transition-property: background-color, color;
    transition-duration: .20s;
    border-radius: .5rem;
    background-color:rgb(115, 161, 161);
    color: rgb(186, 216, 228);
    font-size: 2rem;
    border: black solid thick;
}

.main-nmb{
    font-size: 2rem;
    overflow:hidden;
    text-align: right;
    font-weight: 700;
}



.main-nmb h1 {
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:wrap;
    
}

.alt-nmb {
    text-align: right;
    font-size: 1.3rem;
}

.btn-nmb{
    transition-property: background-color, color;
    transition-duration: .15s;
    border-radius: .5rem;
    background-color:rgb(128, 177, 177);
    color: black;
    font-size: 2rem;
    border: black solid thick;
}

.btn-nmb-hover{
    transition-property: background-color, color;
    transition-duration: .20s;
    border-radius: .5rem;
    background-color:rgb(115, 161, 161);
    color: rgb(186, 216, 228);
    font-size: 2rem;
    border: black solid thick;
}

.btn-nmb-clicked{
    transition-property: background-color, color, box-shadow, -webkit-box-shadow;
    -webkit-box-shadow: inset 0px 0px .33rem .33rem #00000010; 
    box-shadow: inset 0px 0px .33rem .33rem #00000010;
    transition-duration: .10s;
    border-radius: .5rem;
    background-color:rgb(96, 124, 124);
    color:rgba(255, 255, 255, 0.404);
    font-size: 2rem;
    border: black solid thick;
}
.btn-clicked{
    transition-property: background-color, color, box-shadow, -webkit-box-shadow;
    -webkit-box-shadow: inset 0px 0px .33rem .33rem #00000010; 
    box-shadow: inset 0px 0px .33rem .33rem #00000010;
    transition-duration: .10s;
    border-radius: .5rem;
    background-color:rgb(96, 124, 124);
    color:rgba(255, 255, 255, 0.404);
    font-size: 2rem;
    border: black solid thick;
}

.answer-give{
    font-size: 2rem;
    background-color: rgb(241, 167, 182);
    color: black;
    border: black solid thick;
    transition-property: background-color;
    transition-duration: .30s;
    border-radius: .5rem;
    border: black solid thick;
    
}

.answer-give-hover{
    font-size: 2rem;
    background-color: rgb(209, 134, 149);
    color: rgb(241, 222, 222);
    border: black solid thick;
    transition-property: background-color;
    transition-duration: .15s;
    border-radius: .5rem;
    border: black solid thick;
    
}

.answer-give-clicked{
    transition-property: background-color, color, box-shadow, -webkit-box-shadow;
    -webkit-box-shadow: inset 0px 0px .33rem .33rem #00000010; 
    box-shadow: inset 0px 0px .33rem .33rem #00000010;
    transition-duration: .15s;
    background-color: rgb(182, 97, 115);
    color: rgba(240, 235, 235, 0.438);
    border: black solid thick;
    border-radius: .5rem;
    border: black solid thick;
    font-size: 2rem;
}

